.animation-container {
  position: relative; }

.animation-canvas {
  position: absolute;
  top: 40%;
  left: 0;
  width: 100%;
  height: 100%; }

.ball-container {
  width: 20px;
  height: 20px;
  display: inline-block; }

.ball-container-1 {
  transform-origin: top; }

.ball-container-2 {
  transform-origin: left; }

.line-container {
  width: 20px;
  height: 20px;
  display: inline-block;
  border-radius: 20px; }
  .line-container .ball-container {
    padding-left: 20px; }

.ball-head {
  width: 20px;
  height: 20px;
  border-radius: 100%; }

/*.ball-head-2 {
  opacity: 0;
}*/
.blue {
  background: blue; }

.red {
  background: red; }

.green {
  background: green; }

.white {
  background: #fff; }

.orange {
  background: #FF4C05; }

.text-orange {
  color: #FF4C05; }

@keyframes MOVE-LEFT {
  from {
    transform: translate3d(0, 0, 0); }
  to {
    transform: translate3d(-50%, 0, 0); } }

@keyframes MOVE-RIGHT {
  from {
    transform: translate3d(-50%, 0, 0); }
  to {
    transform: translate3d(0, 0, 0); } }

.gif-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%;
  z-index: 5; }

.gif-slogan {
  position: fixed;
  z-index: 10;
  color: #fff;
  font-size: 32px; }

video {
  position: fixed;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  transform: translateX(-50%) translateY(-50%);
  transition: 1s opacity; }

.text-animation-ad body {
  background: #474F72; }

.text-animation-ad svg {
  width: 300px;
  height: 110px; }

.text-animation-ad path {
  stroke: #FF4C05;
  stroke-width: 2px;
  stroke-linecap: round;
  fill: transparent;
  stroke-dasharray: 200;
  stroke-dashoffset: 200;
  animation: char-anim ease-out 2s forwards;
  animation-delay: 0.5s; }

.text-animation-ad path.white {
  fill: transparent;
  background: transparent;
  stroke: #BFBFBF;
  stroke-width: 1px;
  animation: char-white-anim ease-in-out 2s forwards; }

@keyframes char-white-anim {
  from {
    stroke-dashoffset: 200px; }
  to {
    stroke-dashoffset: 0; } }

@keyframes char-anim {
  from {
    stroke-dashoffset: 200px; }
  to {
    stroke-dashoffset: 0; } }



